<template>
    <div style="width: 55%">
        <div>
            <div style="display: flex;justify-content: space-between">
                <div>
                    <el-input placeholder="..." prefix-icon="el-icon-search"
                              clearable
                              @clear="initSads"
                              style="width: 350px;margin-right: 10px" v-model="keyword"
                              @keydown.enter.native="initSads"></el-input>
                    <el-button icon="el-icon-search" type="primary" @click="initSads">
                        
                    </el-button>
                </div>
                <div>
                    <el-button type="primary" icon="el-icon-plus" @click="AddSadView">
                        
                    </el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="batchDeleteSad"></el-button>
                </div>

            </div>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    ref="multipleTable"
                    stripe
                    border
                    max-height="513"
                    :data="speadds"
                    v-loading="speaddLoading"
                    element-loading-text="..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        align="center"
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="sadid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="sadname"
                        label=""
                        align="left"
                        width="200">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="sadmoney"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        align="center"
                        fixed="right"
                        label="">
                    <template slot-scope="scope">
                        <el-button @click="editSadView(scope.row)" style="padding: 3px" size="mini"></el-button>
                        <el-button @click="deleteSad(scope.row)" style="padding: 3px" size="mini" type="danger"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: flex-end">
            <el-pagination
                    background
                    @current-change="currentChange"
                    @size-change="sizeChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="editSadDialog"
                width="30%">
            <el-form label-position="left" label-width="90px" :model="speadd" status-icon :rules="rules" ref="speaddForm">
                <el-form-item label=": " prop="sadname">
                    <el-input clearable style="width: 290px" prefix-icon="el-icon-edit" v-model.number="speadd.sadname"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="sadmoney">
                    <el-input clearable style="width: 290px" prefix-icon="el-icon-edit" v-model.number="speadd.sadmoney"
                              placeholder="">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editSadDialog = false"> </el-button>
                <el-button type="primary" @click="confirmAddSad"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalSad",
        data(){
            return{
                keyword: null,
                speadds: [],
                speaddLoading: false,
                editSadDialog: false,
                multipleSelection: [],
                title: '',
                page: 1,
                size: 10,
                total: 0,
                speadd: {
                    sadname: '',
                    sadmoney: 0
                },
                rules: {
                    sadname: [{required: true, message: '', trigger: 'blur'}],
                    sadmoney: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }]
                }
            }
        },
        mounted() {
            this.initSads();
        },
        methods: {
            initSads(){
                this.speaddLoading=true;
                let url="/salary/specialadditionaldeduction/?page=" + this.page + '&size=' + this.size;
                if(this.keyword){
                    url+="&sadname="+this.keyword;
                }
                this.getRequest(url).then(resp => {
                    this.speaddLoading = false;
                    if (resp) {
                        this.speadds = resp.data;
                        this.total = resp.total;
                        if(resp.data.length===0){
                            this.$notify.error({
                                title: '',
                                message: ''
                            });
                        }
                    }
                });
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                this.initSads();
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initSads();
            },
            emptySad(){
                this.speadd = {
                    sadname: "",
                    sadmoney: "",
                }
            },
            batchDeleteSad(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.postRequest("/salary/specialadditionaldeduction/deletespeadds/",this.multipleSelection).then(resp => {
                        if (resp) {
                            this.initSads();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            AddSadView(){
                this.emptySad();
                this.title='';
                this.editSadDialog=true;
                this.$refs['speaddForm'].resetFields();
            },
            editSadView(data){
                this.title='';
                this.speadd=Object.assign({}, data)
                this.editSadDialog=true;
            },
            deleteSad(data){
                this.$confirm('' + data.sadname + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/specialadditionaldeduction/" + data.sadid).then(resp => {
                        if (resp) {
                            this.initSads();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            confirmAddSad(){
                if(this.speadd.sadid){
                    this.$refs['speaddForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/salary/specialadditionaldeduction/", this.speadd).then(resp => {
                                if (resp) {
                                    this.editSadDialog=false;
                                    this.initSads();
                                }
                            })
                        }
                    });
                } else {
                    this.$refs['speaddForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/salary/specialadditionaldeduction/", this.speadd).then(resp => {
                                if (resp) {
                                    this.editSadDialog=false;
                                    this.initSads();
                                }
                            })
                        }
                    });
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
</script>

<style scoped>

</style>