<template>
    <div style="width: 65%">
        <div>
            <div style="display: flex;justify-content: space-between">
                <div>
                    <el-input placeholder="..." prefix-icon="el-icon-search"
                              clearable
                              @clear="initWels"
                              style="width: 350px;margin-right: 10px" v-model="keyword"
                              @keydown.enter.native="initWels"></el-input>
                    <el-button icon="el-icon-search" type="primary" @click="initWels">
                        
                    </el-button>
                </div>
                <div>
                    <el-button type="primary" icon="el-icon-plus" @click="AddWelView">
                        
                    </el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="batchDeleteWel"></el-button>
                </div>

            </div>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    ref="multipleTable"
                    stripe
                    border
                    max-height="513"
                    :data="welfares"
                    v-loading="welfareLoading"
                    element-loading-text="..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        align="center"
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="welid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="welname"
                        label=""
                        align="left"
                        width="110">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="traffic"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="catering"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="communication"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="bonus"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="other"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        align="center"
                        fixed="right"
                        label="">
                    <template slot-scope="scope">
                        <el-button @click="editWelView(scope.row)" style="padding: 3px" size="mini"></el-button>
                        <el-button @click="deleteWel(scope.row)" style="padding: 3px" size="mini" type="danger"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: flex-end">
            <el-pagination
                    background
                    @current-change="currentChange"
                    @size-change="sizeChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="editWelDialog"
                width="30%">
            <el-form label-position="left" label-width="90px" :model="welfare" status-icon :rules="rules" ref="welfareForm">
                <el-form-item label=": " prop="welname">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.welname"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="traffic">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.traffic"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="catering">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.catering"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="communication">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.communication"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="bonus">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.bonus"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="other">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="welfare.other"
                              placeholder="">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editWelDialog = false"> </el-button>
                <el-button type="primary" @click="confirmAddWel"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalWelfare",
        data(){
            return{
                keyword: null,
                welfares: [],
                welfareLoading: false,
                editWelDialog: false,
                multipleSelection: [],
                title: '',
                page: 1,
                size: 10,
                total: 0,
                welfare: {
                    welname: '',
                    traffic: 0,
                    catering: 1000,
                    communication: 3,
                    bonus: 40,
                    other: 30
                },
                rules: {
                    welname: [{required: true, message: '', trigger: 'blur'}],
                    traffic: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    catering: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    communication: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    bonus: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    other: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }]
                }
            }
        },
        mounted() {
            this.initWels();
        },
        methods: {
            initWels(){
                this.welfareLoading=true;
                let url="/salary/welfare/?page=" + this.page + '&size=' + this.size;
                if(this.keyword){
                    url+="&welname="+this.keyword;
                }
                this.getRequest(url).then(resp => {
                    this.welfareLoading = false;
                    if (resp) {
                        this.welfares = resp.data;
                        this.total = resp.total;
                        if(resp.data.length===0){
                            this.$notify.error({
                                title: '',
                                message: ''
                            });
                        }
                    }
                });
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                this.initWels();
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initWels();
            },
            emptyWel(){
                this.welfare = {
                    welname: "",
                    traffic: "",
                    catering: "",
                    communication: "",
                    bonus: "",
                    other: ""
                }
            },
            batchDeleteWel(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.postRequest("/salary/welfare/deletewels/",this.multipleSelection).then(resp => {
                        if (resp) {
                            this.initWels();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            AddWelView(){
                this.emptyWel();
                this.title='';
                this.editWelDialog=true;
                this.$refs['welfareForm'].resetFields();
            },
            editWelView(data){
                this.title='';
                this.welfare=Object.assign({}, data)
                this.editWelDialog=true;
            },
            deleteWel(data){
                this.$confirm('' + data.welname + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/welfare/" + data.welid).then(resp => {
                        if (resp) {
                            this.initWels();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            confirmAddWel(){
                if(this.welfare.welid){
                    this.$refs['welfareForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/salary/welfare/", this.welfare).then(resp => {
                                if (resp) {
                                    this.editWelDialog=false;
                                    this.initWels();
                                }
                            })
                        }
                    });
                } else {
                    this.$refs['welfareForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/salary/welfare/", this.welfare).then(resp => {
                                if (resp) {
                                    this.editWelDialog=false;
                                    this.initWels();
                                }
                            })
                        }
                    });
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
</script>

<style scoped>

</style>