<template>
    <div style="width: 55%">
        <div>
            <div style="display: flex;justify-content: space-between">
                <el-button type="primary" icon="el-icon-plus" @click="AddTrView">
                    
                </el-button>
                <el-button type="danger" icon="el-icon-delete" @click="batchDeleteTr"></el-button>
            </div>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    ref="multipleTable"
                    stripe
                    border
                    max-height="513"
                    :data="taxRates"
                    v-loading="taxRateLoading"
                    element-loading-text="..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        align="center"
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="trid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="trmin"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="trmax"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="trpercent"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="quideduction"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        align="center"
                        fixed="right"
                        label="">
                    <template slot-scope="scope">
                        <el-button @click="editTrView(scope.row)" style="padding: 3px" size="mini"></el-button>
                        <el-button @click="deleteTr(scope.row)" style="padding: 3px" size="mini" type="danger"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="editTrDialog"
                width="30%">
            <el-form :model="taxRate" status-icon :rules="rules" ref="taxRateForm">
                <el-form-item label=": " prop="trmin">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="taxRate.trmin"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="trmax">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="taxRate.trmax"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="trpercent">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="taxRate.trpercent"
                              placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label=": " prop="quideduction">
                    <el-input clearable style="width: 250px" prefix-icon="el-icon-edit" v-model.number="taxRate.quideduction"
                              placeholder="">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editTrDialog = false"> </el-button>
                <el-button type="primary" @click="confirmAddTr"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalTr",
        data(){
            return{
                taxRates: [],
                taxRateLoading: false,
                editTrDialog: false,
                multipleSelection: [],
                title: '',
                taxRate: {
                    trmin: 0,
                    trmax: 1000,
                    trpercent: 3,
                    quideduction: 40
                },
                rules: {
                    trmin: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    trmax: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    trpercent: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    quideduction: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }]
                }
            }
        },
        mounted() {
            this.initTrs();
        },
        methods: {
            initTrs(){
                this.taxRateLoading=true;
                this.getRequest("/salary/taxrate/").then(resp => {
                    this.taxRateLoading = false;
                    if (resp) {
                        this.taxRates = resp;
                    }
                });
            },
            emptyTr(){
                this.taxRate = {
                    trmin: "",
                    trmax: "",
                    trpercent: "",
                    quideduction: ""
                }
            },
            batchDeleteTr(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.postRequest("/salary/taxrate/deletetrs/",this.multipleSelection).then(resp => {
                        if (resp) {
                            this.initTrs();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            AddTrView(){
                this.emptyTr();
                this.title='';
                this.editTrDialog=true;
                this.$refs['taxRateForm'].resetFields();
            },
            editTrView(data){
                this.title='';
                this.taxRate=Object.assign({}, data)
                this.editTrDialog=true;
            },
            deleteTr(data){
                this.$confirm('' + data.trid + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/taxrate/" + data.trid).then(resp => {
                        if (resp) {
                            this.initTrs();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            confirmAddTr(){
                if(this.taxRate.trid){
                    this.$refs['taxRateForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/salary/taxrate/", this.taxRate).then(resp => {
                                if (resp) {
                                    this.editTrDialog=false;
                                    this.initTrs();
                                }
                            })
                        }
                    });
                } else {
                    this.$refs['taxRateForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/salary/taxrate/", this.taxRate).then(resp => {
                                if (resp) {
                                    this.editTrDialog=false;
                                    this.initTrs();
                                }
                            })
                        }
                    });
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
</script>

<style scoped>

